import React from "react";
import {
  Card,
  CardBody,
  Input,
  Row,
  Col,
  Button,
  UncontrolledDropdown,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
} from "reactstrap";
import axiosConfig from "../../../axiosConfig";
import { ContextLayout } from "../../../utility/context/Layout";
import { AgGridReact } from "ag-grid-react";
import {  Eye, Edit,Trash2, ChevronDown } from "react-feather";
import "../../../assets/scss/plugins/tables/_agGridStyleOverride.scss";
import "../../../assets/scss/pages/users.scss";
import { Route } from "react-router-dom";
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import SwitchEvent from "../../forms/form-elements/switch/SwitchEvent";
import swal from "sweetalert";
import Switch from "react-switch";

class Diagnosticlist extends React.Component {


  state = {
    checked: false,
    rowData: [],
    paginationPageSize: 20,
    currenPageSize: "",
    getPageSize: "",
    defaultColDef: {
      sortable: true,
      editable: true,
      resizable: true,
      suppressMenu: true,
    },
    columnDefs: [
      {
        headerName: "S.No",
        valueGetter: "node.rowIndex + 1",
        field: "node.rowIndex + 1",
        width: 100,
        filter: true,
        // checkboxSelection: true,
        // headerCheckboxSelectionFilteredOnly: true,
        // headerCheckboxSelection: true,
      },
      // {
      //   headerName: "Image",
      //   field: "userimg",
      //   filter: false,
      //   width: 100,
      //   setColumnVisible: false,
      //   cellRendererFramework: (params) => {
      //     return (
      //       <div className="d-flex align-items-center cursor-pointer">
      //         {params.data.userimg.map((i) => (
      //           <img
      //             className=" rounded-circle  mr-3"
      //             src={i}
      //             alt="user avatar"
      //             height="40"
      //             width="40"
      //           />
      //         ))}
      //       </div>
      //     );
      //   },
      // },
      {
        headerName: "Diagnostic Name",
        field: "fullname",
        filter: true,
        width: 250,
        cellRendererFramework: (params) => {
          return (
            <div>
              <span>{params.data.name}</span>
            </div>
          );
        },
      },

      {
        headerName: "Email ID",
        field: "email	",
        filter: true,
        width: 250,
        cellRendererFramework: (params) => {
          return (
            <div className="d-flex align-items-center cursor-pointer">
              <span>{params.data.email}</span>
            </div>
          );
        },
      },

      {
        headerName: "Diagnostic Number",
        field: "dob	",
        filter: true,
        width: 180,
        cellRendererFramework: (params) => {
          return (
            <div className="d-flex align-items-center cursor-pointer">
            <span>{params.data.mobileNumber}</span>
            </div>
          );
        },
      },
      {
        headerName: "Date",
        field: "dateofregister",
        filter: true,
        width: 200,
        cellRendererFramework: (params) => {
          return (
            <div>
            <span>{params?.data?.createdAt.split('T')[0]}</span>
            </div>
          );
        },
      },


      // {
      //   headerName: "Status",
      //   field: "dateofregister",
      //   filter: true,
      //   width: 150,
      //   cellRendererFramework: () => {
      //     return (
      //       <div>
      //         <span>
      //         <Switch
      //     onChange={this.handleChange}
      //     // checked={this.state.checked}
      //     offColor="#888"
      //     onColor="#00aa00"
      //     offHandleColor="#555"
      //     onHandleColor="#00ff00"
      //     uncheckedIcon={false}
      //     checkedIcon={false}
      //   />
      //         </span>
      //       </div>
      //     );
      //   },
      // },

      {
        headerName: "Actions",
        field: "sortorder",
        width: 150,
        cellRendererFramework: (params) => {
          return (
            <div className="actions cursor-pointer">
              <Route
                render={({ history }) => (
                  <Eye
                    className="mr-50"
                    size="25px"
                    color="green"
                    onClick={() =>
                      history.push(`/diagnostic/Viewdiagnostic/${params.data._id}`)
                    }
                  />
                )}
              />

              <Route
                render={({ history }) => (
                  <Edit
                    className="mr-50"
                    size="25px"
                    color="blue"
                    onClick={() =>
                      history.push(`/diagnostic/Editdiagnostic/${params.data._id}`)
                    }
                  />
                )}
              />
               {/*
              <Trash2
                className="mr-50"
                size="25px"
                color="red"
                onClick={() => {
                  let selectedData = this.gridApi.getSelectedRows();
                  this.runthisfunction(params.data._id);
                  this.gridApi.updateRowData({ remove: selectedData });
                }}
              />
              */}

            </div>
          );
        },
      },
    ],
  };
  handleChange = (status) => {
    this.setState({ status }, () => {
      console.log(status);
      // this.updateStatus();
    });
  };
  updateStatus = async () => {
    const { id } = this.props.match.params;
    const { status } = this.state;

    try {
      await axiosConfig.put(`/admin/pharma-update-status/${id}`, { status });
      console.log('Status updated successfully');
    } catch (error) {
      console.error('Error updating status:', error);
    }
  };

  async componentDidMount() {
    await axiosConfig.get(`/admin/diagnostic-list`).then((response) => {
      let rowData = response.data.Diagnostic;
      console.log(rowData);
      this.setState({ rowData });
    });
  }

  async runthisfunction(id) {
    swal(
      `Sure You Want To Delete It`,
      "Delete Or Cancel",

      {
        buttons: {
          cancel: "Cancel",
          catch: { text: "Delete ", value: "delete" },
        },
      }
    ).then((value) => {
      switch (value) {
        case "delete":
          axiosConfig.get(`/admin/dltuser/${id}`).then(
            (response) => {
              this.componentDidMount();
              console.log(response);
            },
            (error) => {
              console.log(error);
            }
          );
          break;
        default:
          break;
      }
    });
  }
  onGridReady = (params) => {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages(),
    });
  };
  updateSearchQuery = (val) => {
    this.gridApi.setQuickFilter(val);
  };
  filterSize = (val) => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val));
      this.setState({
        currenPageSize: val,
        getPageSize: val,
      });
    }
  };
  render() {
    const { rowData, columnDefs, defaultColDef } = this.state;
    const { checked } = this.state;
    return (
      console.log(rowData),
      (
        <div>
          {/* <Breadcrumbs
            breadCrumbTitle="User List"
            breadCrumbParent="Diagnostic"
            // breadCrumbActive=" Diagnostic List"
          /> */}

          <Row className="app-user-list">
            <Col sm="12"></Col>
            <Col sm="12">
              <Card>
                <Row className="m-2">
                  <Col>
                    <h1 sm="6" className="float-left">
                    Diagnostic List
                    </h1>
                  </Col>{/*
                  <Col>
                    <Route
                      render={({ history }) => (
                        <Button
                          className=" btn btn-success float-right"
                          onClick={() => history.push("/user/userlistform/AddDiagnostic")}
                        >
                          Add Diagnostic
                        </Button>
                      )}
                    />
                    </Col>*/}
                </Row>
                <CardBody>
                  {this.state.rowData === null ? null : (
                    <div className="ag-theme-material w-100 my-2 ag-grid-table">
                      <div className="d-flex flex-wrap justify-content-between align-items-center">
                        <div className="mb-1">
                          <UncontrolledDropdown className="p-1 ag-dropdown">
                            <DropdownToggle tag="div">
                              {this.gridApi
                                ? this.state.currenPageSize
                                : "" * this.state.getPageSize -
                                  (this.state.getPageSize - 1)}{" "}
                              -{" "}
                              {this.state.rowData?.length -
                                this.state.currenPageSize *
                                  this.state.getPageSize >
                              0
                                ? this.state.currenPageSize *
                                  this.state.getPageSize
                                : this.state.rowData?.length}{" "}
                              of {this.state.rowData?.length}
                              <ChevronDown className="ml-50" size={15} />
                            </DropdownToggle>
                            <DropdownMenu right>
                              <DropdownItem
                                tag="div"
                                onClick={() => this.filterSize(20)}
                              >
                                20
                              </DropdownItem>
                              <DropdownItem
                                tag="div"
                                onClick={() => this.filterSize(50)}
                              >
                                50
                              </DropdownItem>
                              <DropdownItem
                                tag="div"
                                onClick={() => this.filterSize(100)}
                              >
                                100
                              </DropdownItem>
                              <DropdownItem
                                tag="div"
                                onClick={() => this.filterSize(134)}
                              >
                                134
                              </DropdownItem>
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </div>
                        <div className="d-flex flex-wrap justify-content-between mb-1">
                          <div className="table-input mr-1">
                            <Input
                              placeholder="search..."
                              onChange={(e) =>
                                this.updateSearchQuery(e.target.value)
                              }
                              value={this.state.value}
                            />
                          </div>
                          <div className="export-btn">
                            <Button.Ripple
                              color="primary"
                              onClick={() => this.gridApi.exportDataAsCsv()}
                            >
                              Export as CSV
                            </Button.Ripple>
                          </div>
                        </div>
                      </div>
                      <ContextLayout.Consumer>
                        {(context) => (
                          <AgGridReact
                            gridOptions={{}}
                            rowSelection="multiple"
                            defaultColDef={defaultColDef}
                            columnDefs={columnDefs}
                            rowData={rowData}
                            onGridReady={this.onGridReady}
                            colResizeDefault={"shift"}
                            animateRows={true}
                            floatingFilter={false}
                            pagination={true}
                            paginationPageSize={this.state.paginationPageSize}
                            pivotPanelShow="always"
                            enableRtl={context.state.direction === "rtl"}
                          />
                        )}
                      </ContextLayout.Consumer>
                    </div>
                  )}
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      )
    );
  }
}
export default Diagnosticlist;
